body{background-color:#91baa8}.form{background-color:#15172b;border-radius:20px;box-sizing:border-box;height:500px;padding:20px;width:320px}.form2{background-color:#303245;border-radius:20px;box-sizing:border-box;color:#fff;height:230px;padding:20px;width:400px;box-sizing:border-box;justify-content:center;margin-bottom:10px}.title{color:#eee;font-family:sans-serif;font-size:36px;font-weight:600;margin-top:30px}.subtitle{color:#eee;font-family:sans-serif;font-size:16px;font-weight:600;margin-top:10px}.input-container{height:50px;position:relative;width:100%}.ic1{margin-top:40px}.ic2{margin-top:30px}.input{background-color:#303245;border-radius:12px;border:0;box-sizing:border-box;color:#eee;font-size:18px;height:100%;outline:0;padding:4px 20px 0;width:100%}.cut{background-color:#15172b;border-radius:10px;height:20px;left:20px;position:absolute;top:-20px;-webkit-transform:translateY(0);transform:translateY(0);transition:-webkit-transform 200ms;transition:transform 200ms;transition:transform 200ms, -webkit-transform 200ms;width:76px}.cut-short{width:50px}.input:focus~.cut,.input:not(:placeholder-shown)~.cut{-webkit-transform:translateY(8px);transform:translateY(8px)}.placeholder{color:#65657b;font-family:sans-serif;left:20px;line-height:14px;pointer-events:none;position:absolute;-webkit-transform-origin:0 50%;transform-origin:0 50%;transition:color 200ms,-webkit-transform 200ms;transition:transform 200ms,color 200ms;transition:transform 200ms,color 200ms,-webkit-transform 200ms;top:20px}.input:focus~.placeholder,.input:not(:placeholder-shown)~.placeholder{-webkit-transform:translateY(-30px) translateX(10px) scale(0.75);transform:translateY(-30px) translateX(10px) scale(0.75)}.input:not(:placeholder-shown)~.placeholder{color:#808097}.input:focus~.placeholder{color:#d50834}.submit{background-color:#08d;border-radius:12px;border:0;box-sizing:border-box;color:#eee;cursor:pointer;font-size:18px;height:50px;margin-top:38px;text-align:center;width:100%}.delete{background-color:#d00;border-radius:12px;border:0;color:#eee;cursor:pointer;font-size:15px;margin-left:40px;margin-top:0%;height:50px;outline:0;text-align:center;width:20%}.Complete{background-color:#00dd1d;border-radius:12px;border:0;color:#eee;cursor:pointer;font-size:15px;margin-left:150px;margin-top:-50%;height:50px;outline:0;text-align:center;width:25%}.submit:active{background-color:#06b}.dropbtn{background-color:#3498db;color:#fff;padding:16px;font-size:16px;border:none;cursor:pointer}.select-dropdown{position:relative;margin-top:0ch;background-color:#e6e6e6;width:auto;float:left;max-width:100%;border-radius:2px}.select-dropdown select{font-family:"helvetica neue",helvetica;font-size:1rem;font-weight:200;max-width:100%;padding:8px 24px 8px 10px;border:none;background-color:rgba(0,0,0,0);-webkit-appearance:none;appearance:none}.select-dropdown select:active,.select-dropdown select:focus{outline:none;box-shadow:none}.select-dropdown:after{content:" ";position:absolute;top:50%;margin-top:-2px;right:8px;width:0;height:0;border-left:5px solid rgba(0,0,0,0);border-right:5px solid rgba(0,0,0,0);border-top:5px solid #aaa}body{background-color:#eeedf2;font-family:Arial;font-size:12px;text-align:left}.multi-button{background-color:#fff;border:1px solid #fff;border-radius:14px;padding:7px;box-shadow:0 4px 8px 0 rgba(0,0,0,.1),0 6px 15px 0 rgba(0,0,0,.1);place-items:center;margin-bottom:10px}#cut{background-color:#f15555;border:1px solid #f15579}#copy{background-color:#2745a6;border:1px solid #27a64b}#paste{background-color:#f2b92a;border:1px solid #f2b92a}.button{box-shadow:0 4px 8px 0 rgba(0,0,0,.09),0 6px 15px 0 rgba(0,0,0,.09);padding:10px 17px 10px 17px;font:15px Ubuntu;color:#fff;border-radius:7px;margin-left:10px}.button span{cursor:pointer;display:inline-block;position:relative;transition:.5s}.button span:after{position:absolute;opacity:0;top:0;right:-20px;transition:.5s}.button:hover span{padding-right:25px}.button:hover span:after{opacity:10;right:0}#cut span:after{font-family:FontAwesome;content:""}#copy span:after{font-family:FontAwesome;content:""}#paste span:after{font-family:FontAwesome;content:""}.displist{margin-left:500px;margin-top:-490px}.title4{font-family:sans-serif;padding:7px;align-items:center}
